import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import { useSelector, useDispatch } from 'react-redux';
import { filterEvents } from '../actions';
import { searchEvents } from '../actions';



const useStyles = makeStyles({
  root: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
  icon: {
    borderRadius: '50%',
    width: 16,
    height: 16,
    marginLeft: 30,
    marginTop: -5,
    marginBottom: 5,
    boxShadow: 'inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)',
    backgroundColor: '#f5f8fa',
    backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
    '$root.Mui-focusVisible &': {
      outline: '2px auto rgba(19,124,189,.6)',
      outlineOffset: 2,
    },
    'input:hover ~ &': {
      backgroundColor: '#ebf1f5',
    },
    'input:disabled ~ &': {
      boxShadow: 'none',
      background: 'rgba(206,217,224,.5)',
    },
  },
  checkedIcon: {
    backgroundColor: '#137cbd',
    backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))',
    '&:before': {
      display: 'block',
      width: 16,
      height: 16,
      backgroundImage: 'radial-gradient(#fff,#fff 28%,transparent 32%)',
      content: '""',
    },
    'input:hover ~ &': {
      backgroundColor: '#106ba3',
    },
  },
});

// Inspired by blueprintjs
function StyledRadio(props) {
  const classes = useStyles();

  return (
    <Radio
      className={classes.root}
      disableRipple
      color="default"
      checkedIcon={<span className={clsx(classes.icon, classes.checkedIcon)} />}
      icon={<span className={classes.icon} />}
      {...props}
    />
  );
}



export default function CustomizedRadios() {
    const events = useSelector(state => state.events)
    const dispatch = useDispatch()
    let filteredEvents

    const sortEvents = (filterInput) => {
        switch(filterInput){
            case "alpha":
            filteredEvents = events.sort((a,b) => a.name > b.name ? -1 : 1)
            case "date":
            filteredEvents = events.sort((a,b) => a.date_info[0].date > b.date_info[0].date ? 1 : -1)
            default:
                //adding to array 
            filteredEvents = events.filter(event => event.name.includes(filterInput))
        }
        dispatch(filterEvents(filteredEvents))
    }
  
    return (
        <div>
                {/* <TextField
                    id="outlined-size-small"
                    variant="outlined"
                    size="small"
                    onChange={(event) => sortEvents(event.nativeEvent.data)}
                    />
         */}
        <FormControl component="fieldset">
        <RadioGroup defaultValue="alpha" name="customized-radios" onChange={(event) => sortEvents(event.target.value)}>
            <FormControlLabel value="alpha" control={<StyledRadio />} label="Alpha" />
            <FormControlLabel value="date" control={<StyledRadio />} label="Date" />
        </RadioGroup>
        </FormControl>
        </div>
    );
}
